@import url(https://fonts.googleapis.com/css2?family=Nunito);
body {
    margin: 0;
    font-family: 'Nunito', sans-serif;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1 {
    text-align: center;
}

.Container {
    font-style: 'Nunito', sans-serif;
    padding-bottom: .5rem;
    padding-top: 3rem;
    max-width: 100%;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

@media (min-width: 576px) {
    .Container {
        max-width: 540px;
    }
}

@media (min-width: 768px) {
    .Container {
        max-width: 720px;
    }
}

@media (min-width: 992px) {
    .Container {
        max-width: 960px;
    }
}

@media (min-width: 1200px) {
    .Container {
        max-width: 1140px;
    }
}

.Row {
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
    transition: all 0.5s;
}
.TeamCardWrapper {
    max-width: 90%;
    margin: 20px auto;
}

@media (min-width: 610px) {
    .TeamCardWrapper {
        max-width: 80%;
    }
}

@media (min-width: 768px) {
    .TeamCardWrapper {
        width: 45%;
    }
}

@media (min-width: 992px) {
    .TeamCardWrapper {
        width: 30%;
    }
}

.Card {
    position: relative;
    border-radius: 20px;
    min-height: 300px;
    font-size: large;
    box-shadow: 0 1rem 3rem rgba(0, 0, 0, .175);
}

.Card .user-picture {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    right: 0;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 20px;
    transition: all 0.5s ease-in-out;
}

.Card:hover .user-picture {
    position: absolute;
    z-index: 2;
    top: -40px;
    right: 30px;
    padding: 10px;
    height: 100px;
    width: 100px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 60px;
}

.Card .description {
    font-size: 17px;
    color: rgb(82, 81, 81);
}

h4 {
    font-size: 1.5rem;
    margin-bottom: .5rem;
    font-weight: 700;
    line-height: 1.2;
    margin-top: 0;
}

.Card .role {
    font-size: 18px;
    font-weight: 800;
    margin-top: 0;
}

.Card .icon {
    color: black;
    padding: 12px;
    width: 19px;
    text-align: center;
    text-decoration: none;
    border-radius: 50%;
    transition: all 500ms;
}

.Card .icon:hover {
    color: white;
    background-color: black;
}

.Card .user-name {
    max-width: 60%;
}

.CardBody {
    padding: 25px;
}

.loading__div {
    width: 100%;
    text-align: center;
    margin-top: 50px;
}
input,
select,
button {
    color: #fff;
    font-family: Nunito;
    padding: 0;
    margin: 0;
    border: 0;
    background-color: transparent;
}

#cover {
    padding: 20px;
    margin-bottom: 35px;
    background-color: #ff7575;
    border-radius: 20px;
    box-shadow: 0 10px 40px #ff7c7c;
    width: 80%;
}

.filterOption {
    padding: 20px;
    margin-bottom: 35px;
    background-color: #ff7575;
    border-radius: 20px;
    box-shadow: 0 10px 40px #ff7c7c;
    width: 10%;
    margin-right: 10px;
}

@media (max-width:1000px) {
    #cover {
        width: 60%;
    }
    .filterOption {
        width: 25%;
    }
}

@media (max-width:600px) {
    #cover {
        margin: 10px 50px;
        width: 100%;
    }
    .filterOption {
        margin: 10px 50px;
        width: 100%;
    }
}

#cover form {
    height: 25px;
}

input,
select {
    width: 100%;
    height: 25px;
    font-size: 20px;
    line-height: 1;
}

input:active,
input:focus,
select:active,
select:focus {
    outline: none;
}

input::-webkit-input-placeholder {
    color: #e16868;
}

input:-ms-input-placeholder {
    color: #e16868;
}

input::placeholder {
    color: #e16868;
}

.tb {
    position: relative;
}

.s-icon {
    font-size: 30px;
    color: white;
    position: absolute;
    right: 0;
}

option {
    color: black;
}
